import React, { useState } from "react"
import { Radio, Input } from "antd"

export default function Selection({ radioValue, setRadioValue }) {
  const onChange = e => {
    setRadioValue(e.target.value)
  }

  const radioStyle = {
    display: "block",
    height: "30px",
    lineHeight: "30px",
  }

  return (
    <div>
      <Radio.Group onChange={onChange} value={radioValue}>
        <Radio style={radioStyle} value={"F700000103"}>
          Paris [Austerlitz - Station ultrason (UF)]
        </Radio>
        <Radio style={radioStyle} value={"F664000104"}>
          Gournay-sur-Marne
        </Radio>
      </Radio.Group>
    </div>
  )
}
